.welcome {
  width: 100%;
  height: 100vh;
  padding: 70px 0 0 0;
  background-position: right;
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.welcome-greeting {
  margin-left: 8vw;
  width: 32vw;
  margin-top: 20vh;
  font-size: 40px;
  font-weight: 900;
  letter-spacing: 2px;
}

.welcome-background {
  background-position: center;
  background-repeat: no-repeat;
  background-size: 130px 130px;
  width: 400px;
  height: 600px;
  margin: 0 0 0 1vw;
  right: 0;
  -ms-flex-item-align: baseline;
      align-self: baseline;
}

.welcome-paragraph {
  margin-left: 8vw;
  width: 40vw;
  margin-top: 4vh;
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 1px;
  line-height: 26px;
}


